.ui-contact{margin-top:100px;flex-wrap:wrap}.ui-contact,.ui-contact__section{display:flex;justify-content:center;align-items:center}.ui-contact__section{background-color:#fff;border-radius:3px;overflow:hidden;padding:15px;width:300px;height:150px;flex-direction:column;margin:20px;box-shadow:0 0 3px 0 rgba(0,0,0,.1)}.ui-contact__section__title{font-size:18px;text-transform:uppercase;font-weight:500;letter-spacing:1px;margin-bottom:10px;font-family:Alegreya,serif;line-height:2em;background-image:linear-gradient(180deg,transparent 92%,rgba(252,212,19,.5) 0)}.ui-contact__section__value{font-size:14px;font-weight:500;letter-spacing:1px}.dark .ui-contact__section{background-color:#333}