.ui-posts{margin-top:50px;flex-direction:column;align-items:center}.ui-posts,.ui-posts__list{display:flex;justify-content:center}.ui-posts__list{flex-wrap:wrap}.ui-posts__list__item{background-color:#fff;border-radius:3px;overflow:hidden;padding:15px 30px;width:360px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:20px;box-shadow:0 0 3px 0 rgba(0,0,0,.1)}.ui-posts__list__item__title{font-size:14px;text-transform:uppercase;font-weight:500;letter-spacing:1px;margin-bottom:10px;text-align:center;line-height:1.3}.ui-posts__list__item__description{font-size:12px;line-height:1.3;text-align:center;color:#666}.ui-posts__explore{font-size:14px;font-weight:700;text-transform:uppercase;letter-spacing:1px;border:none;padding:10px 0;cursor:pointer;background-image:linear-gradient(177.4deg,transparent 90%,rgba(252,212,19,.5) 0);margin-top:50px;flex:0 0 auto;display:block}.ui-posts-preloader{margin-top:50px;display:flex;justify-content:center;align-items:center;min-height:300px}.dark .ui-posts__list__item{background-color:#333}.dark select{background:#656565}