a,abbr,acronym,address,applet,article,aside,audio,b,big,blockquote,body,button,canvas,caption,center,cite,code,dd,del,details,dfn,div,dl,dt,em,embed,fieldset,figcaption,figure,footer,form,h1,h2,h3,h4,h5,h6,header,hgroup,html,i,iframe,img,ins,kbd,label,legend,li,main,mark,menu,nav,object,ol,output,p,pre,q,ruby,s,samp,section,small,span,strike,strong,sub,summary,sup,table,tbody,td,tfoot,th,thead,time,tr,tt,u,ul,var,video{margin:0;padding:0;border:0;font-size:100%;font:inherit;vertical-align:baseline;-webkit-tap-highlight-color:rgba(255,255,255,0);box-sizing:border-box}article,aside,details,figcaption,figure,footer,header,hgroup,main,menu,nav,section{display:block}[hidden]{display:none}body{line-height:1}ol,ul{list-style:none;padding:0;margin:0}blockquote,q{quotes:none}blockquote:after,blockquote:before,q:after,q:before{content:"";content:none}table{border-collapse:collapse;border-spacing:0}button{outline:none;border:0;background:transparent}a{cursor:pointer;color:unset;text-decoration:none}input{padding:0;outline:none}body,html{font-size:10px;font-family:Alegreya Sans,sans-serif;background-color:#fafafa;transition:background-color .5s linear,color .5s linear;color:#333}body.dark,html.dark{background-color:#202124;color:#fff}body.dark .ui-about__bg,html.dark .ui-about__bg{display:none}body.dark .ui-about__block__content li a,html.dark .ui-about__block__content li a{background-image:linear-gradient(180deg,transparent 90%,#fff 0)}@media (max-width:839px){body{padding:0 20px}}h1,h2,h3{font-family:Alegreya,serif;margin:0}h2{font-size:2.8rem}strong{font-weight:700}p{margin:0 0 1.8rem}li,p{font-family:Alegreya Sans,sans-serif;font-size:1.8rem;font-weight:400;line-height:1.6}figure{margin:0;box-shadow:10px 10px 30px 0 rgba(30,30,30,.2);vertical-align:top;display:inline-block}figure img{display:block;width:100%;margin:0;padding:0}nav a,p a{color:#000;font-weight:700;text-decoration:none;background-image:linear-gradient(180deg,transparent 90%,#000 0);transition:all .2s}