.ui-about{max-width:700px;margin:0 auto;display:flex;flex-direction:column;justify-content:center;align-items:center;position:relative}.ui-about__bg{width:90%;height:96%;transform:rotate(-5deg);margin-left:5%;margin-top:2%;position:absolute;background:linear-gradient(135deg,#fefcea,rgba(255,248,171,.9));box-shadow:10px 10px 30px 0 rgba(248,231,28,.1);opacity:.3}.ui-about__bg__2{transform:rotate(-2deg);opacity:.8}.ui-about__container{margin:0;padding:80px 0 60px}@media (max-width:839px){.ui-about__container{padding-top:0}}.ui-about__container__bio li,.ui-about__container__contact li{position:relative;list-style-type:none}.ui-about__container__bio li:before,.ui-about__container__contact li:before{content:"â—†";white-space:pre;color:#fcd413;display:inline-block;margin:0 10px 0 -27px;width:17px;position:absolute;top:6px;font-size:11px;font-weight:600;line-height:14px;text-align:center;background-color:rgba(252,212,19,.5);padding:2px 0 1px;border-radius:2px}.ui-about__container__contact .ui-about__bg{background:linear-gradient(135deg,rgba(237,255,238,.9),rgba(190,249,238,.8));box-shadow:10px 10px 30px 0 rgba(238,255,238,.3)}.ui-about__container__contact .ui-about__block__content .page-title span{background-image:linear-gradient(180deg,transparent 85%,rgba(173,243,235,.8) 0)}.ui-about__container__contact ul li:before{color:#a2d6a5;background-color:rgba(217,235,218,.9)}.ui-about__block{width:960px;margin:0 auto;position:relative}@media (max-width:839px){.ui-about__block{width:100%}}.ui-about__block__content{position:relative;width:57%;display:inline-block;margin:0;padding-right:40px}@media (max-width:839px){.ui-about__block__content{width:100%;padding-right:0}}.ui-about__block__content .page-title span{background-image:linear-gradient(180deg,transparent 85%,rgba(252,212,19,.5) 0)}.ui-about__block__content ol,.ui-about__block__content ul{padding-left:3rem;margin:1em 0}.ui-about__block__content li a{font-weight:700;text-decoration:none;background-image:linear-gradient(180deg,transparent 90%,#000 0);transition:all .2s}.ui-about__block figure,.ui-about__block h1{position:relative}.ui-about__block figure{width:42%}@media (max-width:839px){.ui-about__block figure{display:none}}.ui-about__block h1{font-size:3.6rem;margin:0 0 2.4rem}.ui-about__avatar{margin-top:50px;width:250px;height:250px;position:relative}.ui-about__avatar__img{width:250px}.ui-about__bio{margin-top:30px;flex:0 0 auto;text-align:center;font-size:1.8rem;font-weight:400;line-height:1.6}.ui-about__request-cv{font-size:14px;font-weight:700;text-transform:uppercase;letter-spacing:1px;border:none;padding:10px 0;cursor:pointer;background-image:linear-gradient(177.4deg,transparent 90%,rgba(252,212,19,.5) 0);margin-top:40px;flex:0 0 auto;display:block}