.ui-app{max-width:1200px;margin:0 auto}.ui-app__bg{position:absolute;height:100px;width:100%;transform:rotate(3deg);background:linear-gradient(135deg,#fffbb7,#f59071);box-shadow:10px 10px 30px 0 rgba(255,241,225,.1);margin-top:-80px;opacity:.3}.ui-app__bg__container{width:1000px;height:1px;position:relative;margin:0 auto}@media (max-width:839px){.ui-app__bg__container{width:100%}}.ui-app__bg__2{background:linear-gradient(135deg,#f7aa61,#ffe7a8);width:80%;margin-left:10%;transform:rotate(8deg)}.ui-app__logo{font-size:50px;font-weight:700;text-transform:uppercase;text-align:center;padding:20px 0;letter-spacing:2px}.ui-app__logo span{background-image:linear-gradient(178.2deg,transparent 90%,rgba(252,212,19,.5) 0)}.ui-app__nav{display:flex;justify-content:center}.ui-app__nav__item{flex:0 0 auto;padding:5px;margin:0 5px;font-size:12px;text-transform:uppercase;font-weight:500;letter-spacing:1px}.ui-app__nav__item--active{border-bottom:1px solid #666}.ui-app__routes{margin-top:30px}.ui-app__footer{margin-top:40px;border-top:1px solid #eee;padding:20px 10px}.ui-app__footer__message{font-size:1.4rem;display:inline-block}.ui-app__footer__links{display:flex;justify-content:center;margin:10px 0}@media (max-width:839px){.ui-app__footer__links{flex-direction:column;align-items:center}}.ui-app__footer__links__link{flex:0 0 auto;display:flex;margin-right:5px;font-size:14px;text-transform:uppercase;font-weight:700;letter-spacing:.5px;line-height:1.3em}@media (max-width:839px){.ui-app__footer__links__link{margin-bottom:16px}.ui-app__footer__links__link:before{display:none}.ui-app__footer__links__link .link-icon{margin-right:12px!important}.ui-app__footer__links__link .link-title{width:74px}}.ui-app__footer__links__link:first-child:before{display:none}.ui-app__footer__links__link:hover{color:#0077b5}.ui-app__footer__links__link:before{content:"|";padding-right:5px;box-sizing:inherit;color:rgba(0,0,0,.1)}.ui-app__footer__links__link .link-icon{width:16px;height:16px;display:inline-block;position:relative;background-color:rgba(0,0,0,.3);margin-right:4px}.ui-app__footer__links__link.link-medium:hover{color:#76732d}.ui-app__footer__links__link.link-medium:hover .link-icon{background-color:#76732d}.ui-app__footer__links__link.link-medium .link-icon{mask-image:url(/assets/icons/medium.svg);-webkit-mask-image:url(/assets/icons/medium.svg)}.ui-app__footer__links__link.link-github:hover{color:#ab5010}.ui-app__footer__links__link.link-github:hover .link-icon{background-color:#ab5010}.ui-app__footer__links__link.link-github .link-icon{mask-image:url(/assets/icons/github.svg);-webkit-mask-image:url(/assets/icons/github.svg)}.ui-app__footer__links__link.link-twitter:hover{color:#0077b5}.ui-app__footer__links__link.link-twitter:hover .link-icon{background-color:#0077b5}.ui-app__footer__links__link.link-twitter .link-icon{mask-image:url(/assets/icons/twitter.svg);-webkit-mask-image:url(/assets/icons/twitter.svg)}.ui-app__footer__links__link.link-instagram:hover{color:#8a3ab9}.ui-app__footer__links__link.link-instagram:hover .link-icon{background-color:#8a3ab9}.ui-app__footer__links__link.link-instagram .link-icon{mask-image:url(/assets/icons/instagram.svg);-webkit-mask-image:url(/assets/icons/instagram.svg)}.ui-app__footer__source-code{display:flex;justify-content:center;margin:20px 0 10px}.ui-app__footer__source-code__link{flex:0 0 auto;display:flex;margin:0 10px;font-size:10px;text-transform:uppercase;letter-spacing:.5px;color:#999}.ui-mode-switch{position:absolute;top:20px;right:20px}.ui-mode-switch button{cursor:pointer}.dark .ui-app__footer{border-top-color:#5b5c5d}.dark .ui-mode-switch svg path{fill:#fff}