.ui-projects{margin-top:50px;flex-direction:column}.ui-projects,.ui-projects__list{display:flex;justify-content:center;align-items:center}.ui-projects__list{flex-wrap:wrap}.ui-projects__list__item{background-color:#fff;border-radius:3px;overflow:hidden;padding:15px 30px;width:360px;height:150px;display:flex;flex-direction:column;justify-content:center;align-items:center;margin:20px;box-shadow:0 0 3px 0 rgba(0,0,0,.1)}.ui-projects__list__item__title{font-size:18px;text-transform:uppercase;font-weight:500;letter-spacing:1px;margin-bottom:10px;font-family:Alegreya,serif;line-height:2em;background-image:linear-gradient(180deg,transparent 92%,rgba(252,212,19,.5) 0)}.ui-projects__list__item__description{font-size:12px;line-height:1.3;text-align:center;color:#666}.ui-projects__explore{font-size:14px;font-weight:700;text-transform:uppercase;letter-spacing:1px;border:none;padding:10px 0;cursor:pointer;background-image:linear-gradient(177.4deg,transparent 90%,rgba(252,212,19,.5) 0);margin-top:50px;flex:0 0 auto;display:block}.ui-projects-preloader{margin-top:50px;display:flex;justify-content:center;align-items:center;min-height:300px}.dark .ui-projects__list__item{background-color:#333}.dark .ui-projects__list__item__description{color:#bbb}